import React from 'react';
import echarts from 'echarts'


class TuBiao extends React.Component {

  state = {
    myChart: null
  }

  componentDidMount() {
    let chart = echarts.init(document.getElementById("echarts9"));
    this.setState({
      myChart: chart
    }, () => {
      this.setChartOption()
    })
  }

  setChartOption = () => {
    let hours = ['00:00', '04:00', '08:00', '12:00', '16:00', '20:00', '24:00'];
    let days = ['周一', '周二', '周三', '周四', '周五', '周六', '周日'];
    let data = this.props.data;
    var option = {
      grid: {
        left: 2,
        bottom: 10,
        right: 10,
        containLabel: true
      },
      xAxis: {
        type: 'category',
        data: days,
        axisLine: {
          show: false
        }
      },
      yAxis: {
        type: 'category',
        data: hours,
        boundaryGap: false,
        splitLine: {
          show: true,
          lineStyle: {
            color: '#999',
            type: 'dashed'
          }
        },
        axisLine: {
          show: false
        }
      },
      series: [{
        type: 'scatter',
        symbolSize: function (val) {
          return val[2] * 2;
        },
        data: data.map(function (item) {
          return [item[1], item[0], item[2]];
        }),
        animationDelay: function (idx) {
          return idx * 5;
        }
      }]
    };
    this.state.myChart.setOption(option)
  }


  render() {
    return (
      <div className="part9-time">
        <div className="part9-bottom-bg1 out"></div>
        <div className="part9-bottom-bg"></div>
        <div className="part9-bottom">
          <div className="part9-bottom-title">上网时间分布</div>
          <div className="part9-chart" id="echarts9">
          </div>
        </div>
      </div>
    )
  }
}

export default TuBiao