import React, { useEffect } from 'react';
import './index.css';
const echarts = require('echarts');

 export default function TuShuBaoGaoTuBiao (props) {

    useEffect(() => {
        initLine(props.data);
    });
    const initLine=(curData)=>{
        let myChart = echarts.init(document.querySelector('#tushu_line'));
        myChart.setOption({
            title:{
                text:'每月出入图书馆次数',
                left:'center',
                textStyle:{
                    color:'#020D60'
                }
            },
            color:['#2917A1','#F7B500'],
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data:curData.map(item=>{
                    return item.name
                }),
                bottom:'10'
            },
            grid:{
                top:30,
                bottom:60,
                left:40,
                right:40
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data:curData[0].value.map(item=>{
                    return item.month
                })
            },
            yAxis: {
                type: 'value'
            },
            series: curData.map(item=>{
                return {
                    name:item.name,
                    type:'line',
                    stack: '总量',
                    data: item.value.map(ele=>{
                        return ele.value
                    })
                }
            })
        })
    }
    return (
        <div className={'bgOne'}>
            <div className={'bgTwo'}>
                <div id='tushu_line'></div>
            </div>
        </div>
    )
}
