import React, { useEffect } from 'react';
import './index.css';

 export default function TuShuBaoGaoXiangQing (props) {

    useEffect(() => {
        // initLine(props.data);
    });
    const {AverageScore,overflow,courseRank,collgeRank,course,collge} = props.data;
    return (
        <div className={'detail'}>
            <p className={'title '}>本学期图书报告</p>
            <p>本学期你月平均出入图书馆<span>{AverageScore}</span>次,</p>
            <p>高于本专业学生平均（<span>{overflow}</span>）次</p>
            <p>在本专业{course}名同学中排名第<span>{courseRank}</span>名，</p>
            <p>在全校{collge}名同学中排名第<span>{collgeRank}</span>名。</p>
        </div>
    )
}
