import React, { useEffect } from 'react';
import './index.css';

 export default function XueXiChengJiXiangQing (props) {

    useEffect(() => {
        // initLine(props.data);
    });
    const {num,credit,rank,AverageScore,maxCourse,failNum,maxScore} = props.data;
    return (
        <div style={{height:'50%'}} className={'detail'}>
            <p>当前你一共上课<span>{num}</span>门,</p>
            <p>获得<span>{credit}</span>个学分，班级排名<span>{rank}</span></p>
            <p>平均学分成绩<span>{AverageScore}</span>，</p>
            <p><span>{maxCourse}</span>是你得分最高的课程，</p>
            <p>获得了<span>{maxScore}</span>分的优异成绩，</p>
            {
                failNum>0?<p>挂科<span>{failNum}</span>次，失败是成功之母，继续努力～</p>:
                <p>优秀的你从来没有挂过科哦～</p>
            }
            
        </div>
    )
}
