import React from 'react';
import {Icon} from 'antd-mobile';
import './index.css';

class Part4 extends React.Component {
    state = {
        current:0,
        data:{
            context:{},
            tableList:[]
        }
    }
    componentDidMount(){
        let part4Data = JSON.parse(window.sessionStorage.getItem("appInfo"));
        if(part4Data){
            this.setState({
                data:{
                    context:part4Data.part4.context,
                    tableList:part4Data.part4.tableList
                }
            })
        }
    }
    pre = () => {
        if(this.state.current===0){
            return false
        }else{
            this.setState({current:this.state.current-1})
        }
    }
    next = () => {
        if((this.state.current+1)*7>=this.state.data.tableList.length){
            return false
        }else{
            this.setState({current:this.state.current+1})
        }
    }
    render(){
        let list = this.state.data.tableList.slice(7*(this.state.current),7*(this.state.current+1));
        return (
            <div className="item part4bg">
                <div className='title'><span>本学期健康指数</span></div>
                <div className='text'>
                    本学期你一共修了 <span>{this.state.data.context.xuefen}</span> 个学分，<br/> 
                    其中 <span>{this.state.data.context.bixiu}</span> 门必修课、 <span>{this.state.data.context.xuanxiu}</span> 门选修课， <br/> 
                    平均学分成绩 <span>{this.state.data.context.pinjun}</span> 分， <br/> 
                    班级排名第 <span>{this.state.data.context.paiming}</span> 位。<br/> 
                </div>
                <div className='card-outer'>
                    <div className='card-center'>
                        <div className='card-inner'>
                            <div className='card-title'>
                                <div className='first'>课程名称</div>
                                <div className='two'>课程类型</div>
                                <div className='three'>课程成绩</div>
                                <div className='four'>课程排名</div>
                            </div>
                            <div className='card-content'>
                                {
                                    list.map(item=>{
                                        return (
                                            <div className='list-item' key={item.index}>
                                                <div className='first'>{item.name}</div>
                                                <div className='two'>{item.type}</div>
                                                <div className='three'>{item.chengji}</div>
                                                <div className='four'>{item.paiming}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <div className='card-btn'>
                                <span onClick={this.pre} className={`left ${this.state.current===0?"disable":""}`}><Icon type='left'/></span>
                                点击切换更多
                                <span  onClick={this.next} className={`right ${(this.state.current+1)*7>=this.state.data.tableList.length?"disable":""}`}><Icon type='right'/></span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Part4