import React from 'react'


function WenZi(props) {
  return (
    <div className="part11-wenzi">
      <div className="part11-internet-time"><i className="font11" style={{fontSize:21}}>本学期消费分析</i></div>
      <div className="part11-center">
        <div className="part11-center-top">
          <p>你消费次数最多的地方是  </p>
          <p className="part11-color"><i className="font11">{props.data[0].value}</i></p>
        </div>
        <div>
          <p>累计消费金额最多的地方是  </p>
          <p className="part11-color"><i className="font11">{props.data[1].value}</i></p>
          <div>
            <p className="part11-center-top">{props.data[2].value}</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default WenZi