import React from 'react';
import echarts from 'echarts'
import './index.css';

class Part8 extends React.Component{
    state = {
        myChart:null,
        data:{
            data:[],
            total:0,//工多少本
        }
    }
    componentDidMount(){
        let chart = echarts.init(document.getElementById('part8'));
        let part8Data = JSON.parse(window.sessionStorage.getItem("appInfo"));
        if(part8Data){
            this.setState({
                data:{
                    data:part8Data.part8.data,
                    total:part8Data.part8.total
                }
            })
        }
        this.setState({
            myChart:chart
        },()=>{
            this.setChartOption();
        })
    }
    setChartOption = () => {
        const data = this.state.data.data;
        var option = {
            color:["#F26A80","#5FAFF8","#A371EB","#5F64F3","#5EEEED","#60DB85","#F3D75E"],
            title:{
                text: this.state.data.total,
                subtext: '共借阅本数',
                x: 'center',
                y: 'center',
                textStyle: {
                    fontSize:30,
                    fontWeight:'normal',
                    lineHeight:14,
                    color: ['#354F0A']
                },
                subtextStyle: {
                    color: '#666',
                    fontSize: 14
                },
            },
            legend:{
                data:data.map(item=>item.name)
            },
            series:[
                {
                    type:'pie',
                    radius: ['30%', '50%'],
                    data:data,
                    label:{
                        normal:{
                            formatter: '{b}：\n\n{d}%  ',
                        }
                    },
                    itemStyle: {
                        normal: {
                            borderColor: '#fff'
                        },
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        }
        this.state.myChart.setOption(option);   
    }
    render(){
        return (
            <div className="item part8bg">
                <div className='title'><span>本学期图书借阅</span></div>
                <div className='card-outer'>
                    <div className='card-center'>
                        <div className='card-inner'>
                            <div className="card-title">图书借阅类别</div>
                            <div className="card-chart" id='part8'></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Part8