import React from 'react'


function WenZi(props) {
  return (
    <div className="part9-wenzi">
      <div className="part9-internet-time"><i className="font9" style={{fontSize:21}}>本学期上网时间</i></div>
      <div className="part9-center">
        <div className="part9-center-top">
          <p>你本学期周一到周五的上网时间集中在</p>
          <p className="part9-color"><i className="font9">{props.data[0].value}</i></p>
        </div>
        <div>
          <p>周末的上网时间集中在</p>
          <p className="part9-color"><i className="font9">{props.data[1].value}</i></p>
        </div>
        <div>
          <p>你每月平均上网时长为 <span className="part9-color"><i className="font9">{props.data[2].value}</i></span> 小时,</p>
        </div>
        <div>
          <p>高于全校月人均上网时长 <span className="part9-color"><i className="font9">{props.data[3].value}</i></span> 小时。</p>
        </div>
      </div>
    </div>
  )
}

export default WenZi