import React from 'react';
import './index.css';
import XueXiChengJiTuBiao from './XueXiChengJiTuBiao'
import XueXiChengJiXiangQing from './XueXiChengJiXiangQing'


function Part5 (props) {
    
    let state = {
        chartData:[
            {
                year:'2016-2017',
                value:60
            },
            {
                year:'2017-2018',
                value:65
            },
            {
                year:'2018-2019',
                value:82
            }
        ],
        courseDetail:{
            num:12,
            credit:50,
            rank:9,
            AverageScore:91.34,
            maxCourse:'商务英语',
            maxScore:96,
            failNum:1
        }
    }
    let appInfo = window.sessionStorage.getItem('appInfo');
    appInfo = appInfo ? JSON.parse(appInfo) : null;
    state = appInfo.part5;
    return (
        // <div className="item" style={{background: 'purple'}}>
        <div className="item backImage part5">
            <XueXiChengJiTuBiao data = {state.chartData}/>
            <XueXiChengJiXiangQing data = {state.courseDetail}/>
        </div>
    )
}

export default Part5