import React,{useEffect,useState} from 'react';
import Chart from './chart.js';
import './index.css';

function Part3 () {
    const [part3Data,setPart3Data] = useState({listData:{},chartData:[]})
    // const part3Data = {
    //     listData:{
    //         xuexi:'19%',
    //         xiaofei:'61%',
    //         chuxing:'68%',
    //         yinshi:'71%',
    //         shangwang:'31%'
    //     },
    //     chartData:[
            
    //     ]
    // }
    useEffect(()=>{
        let part3Data = JSON.parse(window.sessionStorage.getItem("appInfo"));
        if(part3Data){
            setPart3Data(part3Data.part3)
        }
    },[])
    return (
        <div className="item part3bg">
            <div className='title'><span>本学期健康指数</span></div>
            <div className='list'>
                <div className='list-item'>
                    <span>学习</span>健康指数<div>{part3Data.listData.xuexi}</div>
                </div>
                <div className='list-item'>
                    <span>消费</span>强度指数<div>{part3Data.listData.xiaofei}</div>
                </div>
                <div className='list-item'>
                    周末<span className='center'>出行</span>指数<div>{part3Data.listData.chuxing}</div>
                </div>
                <div className='list-item'>
                    <span>饮食</span>健康指数<div>{part3Data.listData.yinshi}</div>
                </div>
                <div className='list-item'>
                    <span>上网</span>健康指数<div>{part3Data.listData.shangwang}</div>
                </div>
            </div>
            <Chart data={part3Data.chartData}/>
        </div>
    )
}

export default Part3