import React, { useEffect } from 'react';
import './index.css';
const echarts = require('echarts');

 export default function XueXiChengJiTuBiao (props) {

    useEffect(() => {
        initLine(props.data);
    });
    const initLine=(curData)=>{
        let myChart = echarts.init(document.querySelector('#line'));
        myChart.setOption({
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data:curData.map(item=>{
                    return item.year
                }),
            },
            grid:{
                top:20,
                bottom:50,
                left:40,
                right:45
            },
            
            yAxis: {
                type: 'value'
            },
            series: [{
                data:curData.map(item=>{
                    return item.value
                }),
                type: 'line',
                areaStyle: {
                    color:{
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [{
                            offset: 0, color: '#C5E239' // 0% 处的颜色
                        }, {
                            offset: 1, color: '#86A200' // 100% 处的颜色
                        }],
                        global: false // 缺省为 false
                    }
                },

                lineStyle:{
                    color:'#86A200'
                },
                itemStyle:{
                    color:'#86A200'
                }
            }]
        })
    }
    return (
        <div style={{height:'50%',overflow:'hidden'}}>
            <p className={'title '}>学习总体情况</p>
            <div className={'bgOne'}>
                <div className={'bgTwo'}>
                    <div id='line' style={{height:'100%'}}></div>
                </div>
            </div>
            
        </div>
    )
}
