import React from 'react';
import echarts from 'echarts';

class Chart extends React.Component{
    state = {
        myChart:null
    }
    componentDidMount(){
        let chart = echarts.init(document.getElementById('part3'));
        this.setState({
            myChart:chart
        },()=>{
            this.setChartOption();
        })
    }
    setChartOption = () => {
        let data = this.props.data;
        var option = {
            radar:{
                radius:'65%',
		    	triggerEvent: true,
		        name: {
		            textStyle: {//label文字样式
		                color: '#9E4F2C',
                        fontSize:'14',
                        fontWight:'bold',
                        padding:[5, 5]
		            }
		        },
		        nameGap:'2',
		        indicator: data.map(item=>({name:item.name,max:item.max})),
		        splitArea: {
		            areaStyle: {
		            	 color: [
		                         'rgba(218,138,94, 0.1)', 'rgba(218,138,94, 0.2)',
		                         'rgba(218,138,94, 0.4)', 'rgba(218,138,94, 0.6)',
		                         'rgba(218,138,94, 0.8)', 'rgba(218,138,94, 1)'
		                     ].reverse()
		            }
		        },
		        axisLine: { //指向外圈文本的分隔线样式
		            lineStyle: {
		                color: 'rgba(0,0,0,0)'
		            }
		        },
		        splitLine: {
		            lineStyle: {
		            	width:2,
		                color: [
		                    'rgba(224,134,82, 0.1)', 'rgba(224,134,82, 0.2)',
		                    'rgba(224,134,82, 0.4)', 'rgba(224,134,82, 0.6)',
		                    'rgba(224,134,82, 0.8)', 'rgba(224,134,82, 1)'
		                ].reverse()
		            }
		        },
            },
            grid:{
                top:20,
                bottom:0,
                left:40,
                right:40
            },
            series:[
                {
                    type: 'radar',
                    data:[
                        {
                            value:data.map(item=>item.value)
                        }
                    ],
                    areaStyle: {
                        normal: {
                            color: '#DA8A5E'
                        }
                    },
                    symbolSize: 0,
                    lineStyle: {
                        normal: {
                            color: '#EDB195',
                            width: 1
                        }
                    },
                }
            ]
        }
        this.state.myChart.setOption(option);   
    }
    render(){
        return (
            <div className='charts'>
                <div className='container-center'>
                    <div className='content' id='part3'></div>
                </div>
            </div>
        )
    }
}

export default Chart