import React from 'react';
import echarts from 'echarts'


class TuBiao extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      myChart: null
    }
  }




  componentDidMount() {
    let chart = echarts.init(document.getElementById("echarts11"));
    this.setState({
      myChart: chart
    }, () => {
      this.setChartOption()
    })
  }

  setChartOption = () => {
    let data = this.props.data;
    var option = {
      label: 'outside',
      series: [
        {
          name: '',
          type: 'pie',
          radius: '50%',
          center: ['50%', '50%'],
          labelLine: {
            normal: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              borderWidth: 5,
              borderColor: '#fff',
            }
          },
          label: {
            show: true,
          },
          data: data
        }
      ]
    }
    this.state.myChart.setOption(option)
  }


  render() {
    return (
      <div className="part11-time">
        <div className="part11-bottom-bg1 out"></div>
        <div className="part11-bottom-bg"></div>
        <div className="part11-bottom">
          <div className="part11-bottom-title">消费结构</div>
          <div className="part11-chart" id="echarts11">
          </div>
        </div>
      </div>
    )
  }
}

export default TuBiao