import React from 'react'
import { WhiteSpace } from 'antd-mobile'
import WenZi from './components/WenZi'
import TuBiao from './components/TuBiao'
import './index.css'

class Part11 extends React.Component {

  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    let appInfo = window.sessionStorage.getItem('appInfo');
    appInfo = appInfo ? JSON.parse(appInfo) : null;
    return (
      <div className="item-part11">
        {/* 文字部分  S */}
        <div className="part11-top">
          <WenZi data={appInfo.part11.wenziData} />
        </div>
        {/* 文字部分  E */}
        <WhiteSpace size="lg" />
        {/* 图表部分 S */}
        <div className="part11-img">
          <TuBiao data={appInfo.part11.tuBiaoData} />
        </div>
        {/* 图表部分 E */}
      </div>
    )
  }
}

export default Part11