import React from 'react'


function WenZi(props) {
  return (
    <div className="part10-wenzi">
      <div className="part10-internet-time"><i className="font10" style={{fontSize:21}}>本学期消费报告</i></div>
      <div className="part10-center">
        <div className="part10-center-top">
          <p>本学期你使用校园卡消费共计 <span className="part10-color"><i className="font10">{props.data[0].value}</i></span> 次,</p>
        </div>
        <div>
          <p>消费总金额为 <span className="part10-color"><i className="font10">{props.data[1].value}</i></span> 元,</p>
        </div>
        <div>
          <p>平均每月消费 <span className="part10-color"><i className="font10">{props.data[2].value}</i></span> 元,</p>
        </div>
        <div>
          <p>相比于全校学生月消费金额 <span className="part10-color"><i className="font10">{props.data[3].value}</i></span> 元,</p>
        </div>
        <div>
          <p>你可以称为 <span className="part10-color"><i className="font10">{props.data[4].value}</i></span> 。</p>
        </div>
      </div>
    </div>
  )
}

export default WenZi