import React, { useEffect } from 'react';
import './index.css';
const echarts = require('echarts');

 export default function TuShuJieYueTuBiao (props) {

    useEffect(() => {
        initLine(props.data);
    });
    const initLine=(curData)=>{
        let myChart = echarts.init(document.querySelector('#jiyue_line'));
        myChart.setOption({
           title:{
                text: '每月图书借阅次数',
                left:'center',
                textStyle:{
                    color:'#152D51'
                }
            },
            tooltip: {},
            color:['#808FA7','#377C66'],
            // 图例
            legend: {
                data:curData.map(item=>{
                    return item.name
                }),
                bottom:'10'
            },
            grid:{
                top:30,
                bottom:60,
                left:40,
                right:40
            },
            xAxis: {
                data:curData[0].value.map(item=>{
                    return item.month
                }),
                axisLabel: {
                    textStyle: {
                        fontSize: 10,
                        color: '#000'
                    }
                },
            },
            //y轴
            yAxis: {
                type: 'value'
            },
            series: curData.map((item,index)=>{
                return {
                    name: item.name,
                    type: 'bar',//条形图
                    barWidth:15,
                    stack:'总量'+index,
                    data:item.value.map(ele=>{
                        return ele.value
                    })
                }
            })
        })
    }
    return (
            <div className={'bgOne'}>
                <div className={'bgTwo'}>
                    <div id='jiyue_line'></div>
                </div>
            </div>
    )
}
