import React from 'react'
import WenZi from './components/WenZi'
import TuBiao from './components/TuBiao'
import './index.css'

class Part9 extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
        }
    }

    render() {
        let appInfo = window.sessionStorage.getItem('appInfo');
        appInfo = appInfo ? JSON.parse(appInfo) : null;
        return (
            <div className="item-part9">
                {/* 文字部分  S */}
                <div className="part9-top-one">
                    <WenZi data={appInfo.part9.wenziData9}/>
                </div>
                {/* 文字部分  E */}
                {/* 图表部分 S */}
                <div className="part9-top">
                    <TuBiao data={appInfo.part9.tuBiaoData9}/>
                </div>
                {/* 图表部分 E */}
            </div>
        )
    }
}

export default Part9