import React, { useEffect } from 'react';
import './index.css';

 export default function TuShuJieYueXiangQing (props) {

    useEffect(() => {
        // initLine(props.data);
    });
    const {num,AverageScore,low,courseRank,collgeRank,course,collge} = props.data;
    return (
        <div className={'detail'}>
            <p className={'title '}>本学期图书借阅</p>
            <p>本学期你在图书馆借阅记录为<span>{num}</span>本，</p>
            <p>每月平均借阅图书<span>{AverageScore}</span>本，</p>
            <p>低于本专业学生人均（<span>{low}</span>）本，</p>
            <p>在本专业{course}名同学中排名第<span>{courseRank}</span>名，</p>
            <p>在全校{collge}名同学中排名第<span>{collgeRank}</span>名。</p>
        </div>
    )
}
