import React from 'react';
import echarts from 'echarts'


class TuBiao extends React.Component {

  state = {
    myChart: null
  }

  componentDidMount() {
    let chart = echarts.init(document.getElementById("echarts10"));
    this.setState({
      myChart: chart
    }, () => {
      this.setChartOption()
    })
  }

  setChartOption = () => {
    let data = this.props.data;
    var option = {
      legend: {
        show: true,
        bottom: 10,
        left: 'center',
        icon: 'rect',
        itemHeight: 8,
        itemWidth: 8,
        data: data.map(item => {
          return item.name;
        })
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '12%',
        top: '10%',
        containLabel: true
      },
      xAxis: {
        triggerEvent: true,
        type: 'category',
        data: (data[0] && data[0]['value'] && data[0]['value'].map(item => {
          return item.name + '月';
        })) || [],
      },
      yAxis: {
        type: 'value',
        splitLine: {
          show: true,
          lineStyle: {
            type: 'dashed'
          }
        },
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },

      },
      series: data.map(item => {
        return {
          name: item.name,
          type: 'bar',
          barWidth:'15%',
          data: item.value
        };
      })
    };
    this.state.myChart.setOption(option)
  }


  render() {
    return (
      <div className="part10-time">
        <div className="part10-bottom-bg1 out"></div>
        <div className="part10-bottom-bg"></div>
        <div className="part10-bottom">
          <div className="part10-bottom-title">每月图书借阅数</div>
          <div className="part10-chart" id="echarts10">
          </div>
        </div>
      </div>
    )
  }
}

export default TuBiao